<template>
  <el-container>
    <el-header>
      <span class="return" @click="$router.back()"></span>
      <div class="right">
        <!-- <span class="home"></span> -->
        <!-- <span class="share"></span> -->
      </div>
    </el-header>
    <el-main>
      <div v-if="order">
        <strong></strong>
        <p> {{ order.order_sn}}</p>
        <p> {{ order.order_amount}}</p>
      </div>
      <p><strong></strong></p>
      <el-radio-group v-model="radio">
    <el-radio label=""></el-radio>
    <el-radio label=""></el-radio>
  </el-radio-group>
    </el-main>
    <el-footer>
      <!-- <span class="kefu"> </span> -->
      <!-- <router-link to="/cart"></router-link> -->
      <!-- <span class="collect"> </span> -->
      <!-- <el-button type="danger" @click="addCart"></el-button> -->
      <!-- <span class="totalAmount">  {{ cartTotal }} </span> -->
      <el-button type="warning" @click="pay"></el-button>
    </el-footer>
    <login-widget ref="login_widget"></login-widget>
  </el-container>
</template>

<script>
import server from "../server";
import LoginWidget from "./widget/Login";
import global from "../common/global";
export default {
  components: {
    LoginWidget
  },
  data() {
    return {
      order: [],
      radio: '',
    };
  },

  mounted() {
    this.getOrderDetail();
  },

  methods: {
    getOrderDetail: function() {
      var that = this;
      server
        .getOrderDetail(this.$route.params.orderId)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.order = $data.data;
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    },

    pay : function(){
      var params = {payment:this.radio,order_id:this.$route.params.orderId};
      var that = this;
      server
        .pay(params)
        .then(function(response) {
          var $data = response.data;
          if ($data.code == 200) {
            that.order = $data.data;
            that.$router.push(`/pay/success`);
          }
        })
        .catch(function(error, data) {
          that.$message.error(error.response.data.message);
        });
    }
  }
};
</script>
