import '../plugins/axios'

export default {

    // 
    QiNiuUpload: ($data) => axios.post('entry/application/upload',
        $data,
        { headers: { 'Content-Type': 'multipart/form-data' } }
    ),

    // 
    Vote: ($id) => axios.post('entry/application/vote',
        { id: $id }
    ),

    // 
    GetVoteResult: ($id) => axios.get('entry/application/vote'),

    // 
    Login: ($data) => axios.post('login', $data),

    // 
    SendSms: ($data) => axios.post('auth/sendSms', $data),

    // 
    Register: ($data) => axios.post('register', $data),

    // 
    Reset: ($data) => axios.post('password/email', $data),

    // 
    getBanner: () => axios.get('store/ad/getBanner'),

    // 
    getGoodsList: ($data) => axios.get('store/goods/getGoodsList', $data),

    // 
    getGoodsDetail: ($goodsId) => axios.get('store/goods/' + $goodsId + '/detail'),

    // 
    addCart: ($data) => axios.post('store/cart/add', $data),

    // 
    getCartList: ($data) => axios.get('store/cart', $data),

    // 
    changeNum: ($cartId, $data) => axios.put('store/cart/' + $cartId, $data),

    // 
    cartChecked: ($data) => axios.post('store/cart/checked/', $data),

    // 
    cartDelete: ($data) => axios.post('store/cart/delete/', $data),

    // 
    submitOrder: ($data) => axios.post('store/order/submit/', $data),

    // 
    getAddressList: ($data) => axios.get('store/address', $data),

    // 
    addAddress: ($data) => axios.post('store/address', $data),

    // 
    getArea: ($data) => axios.get('area', $data),

    // 
    getCartChecked: () => axios.get('store/cart/getChecked'),

    // 
    getOrderDetail: ($orderId) => axios.get('store/order/' + $orderId),

    // 
    pay: ($data) => axios.post('store/order/pay', $data),

    // 
    profile: () => axios.get('store/user/profile'),

     // 
     getOrderList: () => axios.get('store/order'),
}
